import { Box, Typography } from '@mui/material';
import DownloadButton from './download/DownloadButton';
import ShareButton from './share/ShareButton';

const margin = 3;

export default function MoreInfo() {
    return (
        <>
            <Box
                sx={{ mt: margin, }}
            />
            <ShareButton />
            <Typography
                variant="h8"
                component="div"
            >
                : 
            </Typography>
            <Typography
                variant="h8"
                component="div"
            >
                _#
            </Typography>
            <DownloadButton />
        </>
    );
}