import { Typography } from '@mui/material';
import FileDownloadIcon from '@mui/icons-material/FileDownload';
import onDownload from './onDownload';

const marginTop = 2;

export default function DownloadButton() {
    return (
        <>
            <FileDownloadIcon
                onClick={onDownload}
                fontSize="large"
                color="primary"
                sx={{ mt: marginTop }} />
            <Typography
                sx={{ mb: 3 }}
                variant="h8"
                component="div">
                
            </Typography>
        </>
    )
}